<template>
  <div class="appList">
    <div class="main">
      <div class="top">
        <h2>{{toptitle}}</h2><span>选择应用点击Ok/确认键管理应用</span>
        <div class="btn_num">{{this.option}}/{{this.options}}页，共 {{this.elementList.length}} 个应用</div>
      </div>
      <div class="app_box">
        <ul class="app_ul">
          <li v-for="(item,index) of elementList" :key="'appList'+index" :id="('Img'+index)" :class="{'active':index===iskc}">
            <div class="appListPos">
              <lazy-image :src='("data:image/png;base64,"+item.appIcon)' :placeholder='placeholder' />
              <span>{{item.appName}}</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    <alert-msg ref="appAlert" :isNotImg="isImg" v-on:listernpopup="updateKeyDo" :title="tips" :sure="openApp" :cancle="uninstall"></alert-msg>
  </div>
</template>

<style scoped>
.appList {
  position: relative;
  width: 1280px;
  height: 720px;
  margin-top: 30px;
}
.main {
  position: absolute;
  width: 1195px;
  height: 2000px;
  padding-top: 0px;
  padding-left: 80px;
  overflow: hidden;
  background-color: #2d4158;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.top {
  margin-bottom: 20px;
  font-size: 24px;
}
.top h2 {
  font-size: 45px;
  opacity: 0.64;
  display: inline-block;
  font-family: Helvetica, sans-serif;
  margin-right: 20px;
}
.top div {
  float: right;
  margin-right: 80px;
  padding-top: 20px;
}
.app_box {
  width: 1200px;
  height: 530px;
  padding: 10px;
  overflow: hidden;
}
.appListPos {
  margin: 60px 30px 50px 30px;
  width: 100px;
  height: 100px;
  text-align: center;
  z-index: 10;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.appList li {
  float: left;
  width: 160px;
  height: 233px;
  background-color: black;
  border: 1px solid rgba(112, 112, 112, 1);
  margin-right: 32px;
  margin-bottom: 40px;
  box-sizing: border-box;
}
li span {
  margin-left: -7px;
  display: block;
  width: 110px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.appList li.active {
  border: 3px solid #ffa400;
  transform: scale(1.1);
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.4);
}
</style>

<script>
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import AlertMsg from "../firm";
import {xha} from '../../js/xha.js'
export default {
  name: "AppManage", // 我的-应用管理
  props: ["type"],
  components: {
    AlertMsg
  },
  data() {
    return {
      iskc: 0,
      isShow: true,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      option: 1,
      options: 0,
      position: 1,
      eleId: "",
      round: "round",
      appTitle: "appTitle",
      toptitle: "我的应用",
      tips: "管理应用",
      openApp: "打开应用",
      uninstall: "卸载应用",
      isImg: false,
      elementList: [
        {
          appName: "我的名字很长长长长长",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "我的名字一般般",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "CIBN哭喵影视",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        },
        {
          appName: "YHTV",
          appPackageName: "yanhua.tv.bs.https"
        }
      ],applength:0
    };
  },
  created() {
    window.getAppList = this.getAppList;
    try {
      let jsonArray = yh.getAppList("getAppList");
      this.elementList = JSON.parse(jsonArray);
      this.$store.commit("LOCALAPPSNUM", this.elementList.length); //  获取本地app数量
      console.log("当前app数量为："+this.elementList.length);
      this.applength = this.appsNum;
    } catch (error) {
      console.log("获取本地app应用error");
    }
    this.options = Math.ceil(this.elementList.length / 12);
    if (this.options == 0) {
      this.options = 1;
    }
  },
  mounted() {},
  methods: {
    getAppList(jsonArray) {
      console.log("卸载成功，重新刷新appList");
      try {
        let jsonArray = yh.getAppList("getAppList");
        this.elementList = JSON.parse(jsonArray);
        console.log("客户端回调数量为："+this.elementList.length);
        this.$store.commit("LOCALAPPSNUM", this.elementList.length); //  获取本地app数量
      } catch (error) {
        console.log("卸载成功后获取本地app应用error");
      }
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const element = this.elementList[this.itemNo];
        this.isShow = false;
        this.$store.commit("DOWNLOADAPPNAME",element.appPackageName); //  打开的app名称
        this.$refs.appAlert.dialog(
          true,
          element.appIcon,
          element.appPackageName,
          element.appName
        );
      }
    },
    back() {
      this.$router.go(-1);
    },
    left() {
      if (this.itemNo == 0) return;
      if (this.itemNo % 6 === 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if ((this.itemNo + 1) % 6 === 0) return;
      if (this.itemNo == this.elementList.length - 1) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      if (this.itemNo < 6) {
        return;
      } else {
        this.removeCss();
        this.itemNo -= 6;
        this.addCss();
        let height = $("#Img" + this.itemNo).offset().top;
        if (height < 100) {
          this.option--;
          $(".app_ul").css({
            transition: "all 0.01s",
            transform: "translateY(-" + 546 * (this.option - 1) + "px)"
          });
        }
      }
    },
    down() {
      if (this.itemNo + 6 >= this.elementList.length) {
        return;
      } else {
        this.removeCss();
        this.itemNo += 6;
        this.addCss();
        let height = $("#Img" + this.itemNo).offset().top;
        if (height > 500) {
          this.option++;
          $(".app_ul").css({
            transition: "all 0.01s",
            transform: "translateY(-" + 546 * (this.option - 1) + "px)"
          });
        }
      }
    },
    addCss() {
      this.iskc = this.itemNo;
      $(".appList li")
        .eq(this.itemNo)
        .addClass("hover");
    },
    removeCss() {
      $(".appList li")
        .eq(this.itemNo)
        .removeClass("hover");
    },
    updateKeyDo(args) {
      this.isShow = true;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos","appsNum","downloadAppName"])
  },
  watch: {
    appsNum(){
            console.log(this.applength);
            console.log("我的应用app数量已变化"+this.appsNum);
            if(this.appsNum<this.applength){
               xha.app(this.downloadAppName,'2','4');
            }
            this.applength = this.appsNum;
            console.log("需要更新本地的app数量");
    }
  }
};
</script>



