<template>
  <div class="UserCenter">
    <h1 class="userTitle">个人中心</h1>
    <div class="userInfo">
      <div class="deviceInfo">
        <ul class="deviceName">
          <li>账号</li>
          <li>Mac地址</li>
          <li>开发版本</li>
        </ul>
        <ul class="deviceValue">
          <li>{{deviceInfo.user_id}}</li>
          <li>{{deviceInfo.mac}}</li>
          <li>{{deviceInfo.apk_version_name}}</li>
        </ul>
      </div>
      <div class="UserBill"  v-show="false">
        <ul v-if="true">
          <li v-for="(item,index) in orderList" :key="index+'order'" v-if="index<4">&nbsp;&nbsp;&nbsp;您于{{dateFormat(item.payTime)}}成功开通{{item.productName}}，消费{{item.price/100}}元。</li>
        </ul>
        <div v-if="true" class="moreOrder" :class="{'active':iskc===-1}">我的账单</div>
      </div>
    </div>
    <hr class="catline"/>
    <div class="moreFunction">
      <h2>更多功能</h2>
      <ul>
        <li v-for="(item, index) in elementList" :key="index" :class="{'active':index==itemNo}">
          <span :class="('img')+(index+1)"></span>
          <span>{{item.elementName}}</span>
        </li>
      </ul>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.userTitle {
  width: 190px;
  height: 80px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 44px;
  font-weight: 400;
  color: #6687a7;
  margin: 18px 63px;
}

.userInfo {
  display: flex;
  position: relative;
  width: 1280px;
  height: 300px;
  margin-top: 12px;
}

.deviceInfo {
  width: 310px;
  height: 300px;
  position: relative;
  display: flex;
  margin-left: 72px;
}

.deviceName {
  position: relative;
  width: 100px;
  color: #4296fc;
  font-size: 24px;
  margin-right: 30px;
}

.deviceName li {
  margin-bottom: 70px;
}

.deviceValue li {
  width: 180px;
  height: 40px;
  line-height: 40px;
  margin-bottom: 60px;
  border-bottom: 1px solid #2863b0;
}

.UserBill {
  width: 755px;
  height: 100%;
  margin-left: 55px;
}

.UserBill li {
  margin-bottom: 20px;
  background-color: #8096b3;
  color: #ffffff;
  border-radius: 16px;
  height: 42px;
  text-align: left;
  line-height: 42px;
  padding-left: 25px;
}

.moreOrder {
  position: relative;
  border-radius: 15px;
  width: 181px;
  height: 42px;
  text-align: center;
  line-height: 42px;
  background-color: #ffffff;
  font-size: 24px;
  color: #000000;
}

.moreOrder.active {
  background-color: #0098bc;
  border: 4px solid #fff;
  margin: -3px;
  color: #fff;
  -webkit-box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
}
.catline {
  margin: 20px 72px;
  width: 1121px;
  border: 1px solid #000000;
}

.moreFunction {
  margin-left: 63px;
  width: 1200px;
  height: 230px;
  overflow: hidden;
}

.moreFunction h2 {
  font-size: 30px;
  font-weight: 400;
  padding-left: 10px;
  margin-bottom: 15px;
}

.moreFunction ul {
  display: flex;
}

.moreFunction li {
  min-width: 110px;
  height: 140px;
  border-radius: 19px;
  margin-right: 22px;
  font-size: 20px;
  text-align: center;
  /* color: #4296FC; */
}
.moreFunction li:first-child {
  margin-left: 10px;
}

.moreFunction li.active {
  border: 3px solid #fff;
  margin-right: 16px;
  transform: scale(1.1);
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.4);
}

li span {
  padding-bottom: 10px;
}

.moreFunction li img {
  width: 100%;
  height: 100%;
  border-radius: 19px;
}

.UserCenter {
  position: absolute;
  width: 1280px;
  height: 720px;
  background: linear-gradient(180deg, #184983, #020b33);
  display: flex;
  flex-direction: column;
}

.img0,
.img1,
.img2,
.img3,
.img4,
.img5,
.img6,
.img7,
.img8,
.img9,
.img10 {
  display: inline-block;
  vertical-align: middle;
  background: url(../../assets/spriteImg_100x100.png) 0px 0px no-repeat;
  width: 100px;
  height: 100px;
}

.img0 {
  background-position: 0 0;
}
.img1 {
  background-position: -110px 0;
}
.img2 {
  background-position: -225px 0;
}
.img3 {
  background-position: 0 -115px;
}
.img5 {
  background-position: -110px -115px;
}
.img6 {
  background-position: 0px -335px;
}
.img7 {
  background-position: -225px -115px;
}
.img8 {
  background-position: 0px -225px;
}
.img9 {
  background-position: -220px -225px;
}
.img10 {
  background-position: 0 -335px;
}
/* .img10 {
  background-position: -110px -335px;
} */
</style>
<script>
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import { Authentication as auth } from "../../js/auth.js";
import { HTTPUtil as api } from "../../fetch/api.js";
import  {xha} from '../../js/xha.js'
export default {
  name: "UserCenter",
  props: ["initData"],
  data() {
    return {
      iskc: 0,
      isShow: true,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      position: 1,
      eleId: "",
      elementList: [
        { elementName: "最近观看" },
        { elementName: "我的收藏" },
        { elementName: "本地播放" },
        { elementName: "我的账单" },
        { elementName: "应用管理" },
        { elementName: "系统信息" },
        { elementName: "网络设置" },
        { elementName: "一键加速" },
        { elementName: "垃圾清理" },
        { elementName: "网络测速" },
      ],
      round: "round",
      deviceInfo: {
        reserve_group: "",
        model: "",
        content_id: "",
        terrace_id: "",
        operator_id: "",
        apk_version: "7",
        brand: "",
        site_id: "19",
        user_id: "",
        device_id: "2018042746ac4afeb380cc",
        soft_v: "3.0.1",
        sys_v: "4.4.2",
        mode: "",
        apk_version_name: "0.0.0.0"
      },
      orderList: [
      ],
      link: ""
    };
  },
  created() {
    this.getDeviceInfo();
    window.orderHistory = this.orderHistory;
    // console.log("请求账单信息");
    // let params ={};
    // params.url="/api/OrderList";
    // params.type="post";
    // params.data=JSON.parse(auth.OrderListParams("1","0",1,10));
    // api.ajax(params).then((jsonData)=>{
    //   console.log(JSON.stringify(jsonData));
    //     if(jsonData.code==200){
    //       if(jsonData.result.orders.length>0){
    //         this.orderList = this.changeJson(jsonData.result.orders)
    //       }
    //     }
    // })
    yh.HttpPost(
      auth.OrderList,
      auth.OrderListParams("1", "0", 1, 10),
      "orderHistory"
    );
  },
  mounted() {},
  methods: {
    orderHistory(jsonData) {
      console.log(jsonData);
      if (jsonData.code == 200) {
        this.orderList = jsonData.result.orders;
      }
    },
    getDeviceInfo() {
      try {
        this.deviceInfo = JSON.parse(yh.deviceInfo());
        console.log(this.deviceInfo);
      } catch (error) {
        console.log("用户信息转换异常");
      }
    },
    dateFormat(timedata) {
      if(timedata !== null){
        let year = timedata.substr(0, 4);
        let month = timedata.substr(4, 2);
        let day = timedata.substr(6, 2);
        return year + "年" + month + "月" + day + "日";
      }
    },
    changeJson(jsonDB) {
      //        按时间排序
      let jsontyb = jsonDB;
      let _tempArray = []; //  sort 作用域在windows下面需要注意
      let sortedObjKeys = Object.keys(jsontyb).sort(function(a, b) {
        return jsontyb[b].payTime - jsontyb[a].payTime;
      });
      for (let i in sortedObjKeys) {
        if (sortedObjKeys.hasOwnProperty(i)) {
          _tempArray.push(jsontyb[sortedObjKeys[i]]);
        }
      }
      return _tempArray;
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        console.log("返回返回");    
        if(!this.isSouth){
          this.$router.push({name:'index'});
        }else{
          this.$router.push({name:'south'});
        }
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        return;
        this.up();
      } else if (kc == "KeyEnter") {
        if (this.itemNo === -1) {
          let payfor =
            "http://171.14.99.98:7001/hnitv-api/hd/index.jsp?epg_info=<account>" +
            yh.userId +
            "</account><back_url>" +
            location.href +
            "?pos=8</back_url>";
          xha.pageLoad("个人中心","推荐","更多账单","00","0108","01"); // xha 埋点
          HyBirdWebCallBack.showPage(payfor);
        } else if (this.itemNo === 0) {
          // 最近观看
          xha.pageLoad("个人中心","推荐","最近观看","0"+this.itemNo,"0108","01"); // xha 埋点
          c.routerSkip("", "15", { type: false }, this.$router);
        } else if (this.itemNo === 1) {
          // 我的收藏
          xha.pageLoad("个人中心","推荐","我的收藏","0"+this.itemNo,"0108","01"); // xha 埋点
          c.routerSkip("", "15", { type: true }, this.$router);
        } else if (this.itemNo === 2) {
          // 本地播放
          xha.pageLoad("个人中心","推荐","本地播放","0"+this.itemNo,"0108","01"); // xha 埋点
          yh.openLocalPlay();
        } else if (this.itemNo === 3){
          // 我的账单
          let payfor =
            "http://171.14.99.98:7001/hnitv-api/hd/index.jsp?epg_info=<account>" +
            yh.userId +
            "</account><back_url>" +
            location.href +
            "?pos=8</back_url>";
          xha.pageLoad("个人中心","推荐","我的账单","0"+this.itemNo,"0108","01"); // xha 埋点
          HyBirdWebCallBack.showPage(payfor);
        } else if (this.itemNo === 4) {
          // 应用管理
          xha.pageLoad("个人中心","推荐","应用管理","0"+this.itemNo,"0108","01"); // xha 埋点
          c.routerSkip("", "14", { type: true }, this.$router);
        } else if (this.itemNo === 5) {
          // 系统信息
          xha.pageLoad("个人中心","推荐","系统信息","0"+this.itemNo,"0108","01"); // xha 埋点
          window.localStorage.setItem('roter','user')
           c.routerSkip("", "13", 1, this.$router);          
        } else if (this.itemNo === 6) {
          // 网络设置
          xha.pageLoad("个人中心","推荐","网络设置","0"+this.itemNo,"0108","01"); // xha 埋点
          yh.settingNetwork();         
        } else if (this.itemNo === 7) {
          xha.pageLoad("个人中心","推荐","一键加速","0"+this.itemNo,"0108","01"); // xha 埋点
          // 一键加速
          yh.turboMemory();
        } else if (this.itemNo === 8) {
          xha.pageLoad("个人中心","推荐","垃圾清理","0"+this.itemNo,"0108","01"); // xha 埋点
          // 垃圾清理
          yh.clearRubbish();
        }
      }
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.position == 1) {
        if (this.itemNo >= 8) return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      }
    },
    up() {
      this.position = 0;
      this.iskc = -1;
      this.itemNo = -1;
      this.removeCss();
      this.addCss();
    },
    down() {
      this.position = 1;
      this.iskc = 0;
      this.itemNo = 0;
    },
    addCss() {
      if (this.position == 1) {
        $(".moreFunction li")
          .eq(this.itemNo)
          .addClass("active");
      } else {
        $(".moreOrder").addClass("active");
      }
    },
    removeCss() {
      if (this.position == 1) {
        $(".moreFunction li")
          .eq(this.itemNo)
          .removeClass("active");
      } else {
        $(".moreOrder").removeClass("active");
      }
    }
  },
  watch: {},
  computed: {
    ...mapGetters(["isSouth"])
  },
  beforeRouteLeave(to, from, next) {
    this.$store.commit("GET_HTTPRESPONSETIME", new Date().getTime());
    next();
  }
};
</script>



