<template>
    <div class="yy-confirm" v-show="visible">
      <div class="bg">
        <div v-if="!isApp" v-show="Touninstall" class="appLoading"></div>
      </div>
      <div class="yy-container">
        <div name="footer">
          <div  class="alertMsg">
            <div v-if="isNotImg">{{alertMsg}}</div>
            <div v-if="!isNotImg" class="imgword">
              <img v-if="!Touninstall" :src= "'data:image/png;base64,'+imgSrc" />
              <div  v-if="Touninstall"  class="appLoading"></div>
              <h2 v-if="!isApp">{{alertMsg}}</h2>
            </div>
          </div>
          <div class="yy-footer" slot="footer" >
            <a v-show="btnSure" class="yy-btn make-sure select-on">{{sure}}
              <i class="iconfont icon-tuichu"></i>
            </a>
            <a v-if="!isSure" class="yy-btn cancel">{{cancle}}
              <i class="iconfont icon-fanhui"></i>
            </a>
          </div>
        </div>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
    </div>
</template>
<script type="text/javascript">
  import {client as yh} from '../js/client.js'
  import {xha} from '../js/xha.js'
  export default {
    props: {
      content:{
          type: String,
          default:'我是一只提示语!只出现在关键的时候!'
      },sure:{  // 确定
        type:String,
        default:'退出应用'
      },cancle:{ // 取消
        type:String,
        default:'返回观看'
      },isNotImg:{ // 是否要展示图片
        type:Boolean,
        default:true,
      }
     },
    data :function() {
      return {
        isShow:false,
        num:0,
        visible: false,
        closeVisible: true, 
        placeholder:require('../assets/img_loading_160x230.png'),
        round:'round',
        imgSrc:require('../assets/img_loading_160x230.png'),
        isApp:false, // 判断是否是app操作弹出窗
        packageName:"", // app 名称,
        Touninstall:false, // 是否是卸载操作
        alertMsg:"确定不再停留一会吗!",
        isSure:false,
        title:"退出应用",
        btnSure:true,
        appName:''
      }
    },
    methods: {
      close () {
        this.$emit('update:visible', false)
      },
      showBox (){
        this.visible = true
      },keyCode(kc) {
        if(!this.btnSure)return;
        if (kc == "down") {
          this.down();
        } else if (kc == "up") {
          this.up();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "right") {
          this.right();
        }else if(kc == "KeyEnter"){
          if(this.btnSure){
            this.KeyEnter();
          }
        } else if (kc == "KeyBack") {
            this.KeyBack();
        }
      },
        KeyBack(){
        if(this.btnSure){
            this.visible = false;
            this.isShow = false;
            this.$emit("listernpopup",false);
          }
        }
      ,KeyEnter(){
          if(!this.isApp){ // 首页退出逻辑
            this.visible = false;
            this.isShow = false;
            if (this.num==0){
              this.$emit("listernpopup",true);
            }else {
              this.$emit("listernpopup",false);
             }
          }else{ // app打开卸载逻辑
            if(this.num==0){
              console.log("打开"+this.packageName);
              if(this.$route.name=="appManage"){
                xha.app(this.appName,'2','3');
              }else{
                xha.app(this.appName,'1','3');
              }
              yh.openApp(this.packageName);
              setTimeout(() => {
                   this.visible = false;
                   this.isShow = false;
                   this.$emit("listernpopup",true);
              }, 1000);
            }else if(this.num==1){ // 卸载点击后将键值交换给父组件
              console.log("卸载应用"+this.packageName);
              this.Touninstall = true;
              yh.uninstallApp(this.packageName);
              this.visible = false;
              this.isShow = false;
              this.Touninstall = false;
              this.$emit("listernpopup",true);
            }
          }
         

      },right(){
        this.num++;
        if (this.num>1){
          this.num=1
        }
        $('.make-sure').removeClass('select-on');
        $('.cancel').addClass('select-on');
      },left(){
        this.num--;
        if (this.num<0){
          this.num=0
        }
        $('.cancel').removeClass('select-on');
        $('.make-sure').addClass('select-on');
      },
      updateStast(bl){
//        this.json = jsonquery
        this.isShow = bl;
        this.visible = bl;
      },dialog(bl,imgSrc,packageName,appName){
         this.isShow = bl;
         this.visible = bl;
         this.imgSrc = imgSrc;
         this.isApp = bl; 
         this.appName = appName;
         this.packageName = packageName;
         this.title ="应用管理";
      },warnLog(msg){
        this.alertMsg = msg;
        this.isShow = true;
        this.visible = true;
        this.isSure = true;
        this.title = "认证鉴权";
        this.btnSure = false;
      }
    },
    components: {}
  }
</script>
<style type="text/css" scoped="firm">

  .yy-content{
    color: darkgray;
    height: 200px;
    min-height: 90px;
    text-align: center;
    line-height: 90px;
    font-size: 16px;
    font-family: "proxima nova", "Roboto", "helvetica", Arial, sans-serif;
    background: #ffffff;
  }
  .bounce-enter-active {
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2);
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
  }
  .bounce-leave-active {
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2);
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
  }
  .yy-confirm {
    position: fixed;
    top: 0;
    left: 0;
    width: 1280px;
    height: 720px;
    z-index: 999;
  }
  .yy-confirm .bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.67;
    z-index: 999;
  }
  .yy-confirm .yy-container {
    position: absolute;
    display: inline-block;
    top: 160px;
    left: 283px;
    max-width: 1280px;
    z-index: 999;
    width: 695px;
    height: 343px;
    border: 1px solid #CECECE;
    border-radius: 15px;
    opacity: 0.8;
  }
  .yy-confirm .yy-container .header {
    height: 50px;
    padding-left: 30px;
    line-height: 50px;
    background-color: rgba(21,48,57,0.9);
    border-bottom: 1px rgba(0,128,128,0.95) solid;
    color: #fff;
    text-align: left;
    box-sizing: border-box;
  }

  .yy-btn {
    text-decoration: none !important;
    border-radius: 14px;
    text-align: center;
    width: 178px;
    height: 66px;
    line-height: 66px;
    border: 1px rgba(255,255,255,1) solid;
    background: #152F3A;
    color:#FFFFFF;
    display: inline-block;
    opacity: 1;
  }
  .yy-btn.select-on {
    background-color:#1399B7;
    color: #fff;
    border: 3px rgba(255,255,255,1) solid;
    margin: -3px;
  }
  .yy-close-btn {
    cursor: pointer;
    float: right;
  }
  .yy-confirm .yy-container .yy-footer {
    background-color:#37506C;
    height:133px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    align-items:flex-start;
    border-radius: 0px 0px 15px 15px;
  }
  .alertMsg div:nth-child(1){word-wrap: break-word; word-break: normal;margin-top: 50px; }
  .header i{position: relative;font-size: 25px;margin-right: 10px;}
  .header span{position: relative;top: -2px;display: inline-block;}
  .make-sure i{position: relative;font-size: 29px;top:4px;}
  .cancel i{position: relative;font-size: 24px;top:4px;margin-left: 3px;}
  .alertMsg{background-color:#37506C;height:170px;text-align: center;padding: 20px 60px;font-family: '微软雅黑', Helvetica, sans-serif;font-size: 29px;letter-spacing: 3px;border-radius: 15px 15px 0px 0px;}
  .alertImg{width: 100px;height: 100px;}
  .imgword h2{position: relative;top: -115px;}
  .imgword img{width: 100px;height: 100px;border-radius: 13px;}
  .appLoading{
     width: 120px;
       height: 120px;
       position: relative;
       margin: 0 auto;
       margin-top:240px;
       mask-image:url('../assets/load.png');
       -webkit-mask-image:url('../assets/load.png');
       background:url('../assets/load.png'); 
       line-height:0;
       font-size:0;
       text-align:center;
       animation-name: load;
       animation-duration: 3s;
       animation-iteration-count: infinite;
       animation-timing-function: linear;
       -webkit-animation-name: load;
       -webkit-animation-duration: 2s;
       -webkit-animation-iteration-count: infinite;
       -webkit-animation-timing-function: linear;
       white-space: nowrap;
       overflow: hidden;
  }
   @-webkit-keyframes load{0%{-webkit-transform: rotate(0deg);}100%{-webkit-transform: rotate(360deg);}}
   @keyframes load{0%{-webkit-transform: rotate(0deg);}100%{-webkit-transform: rotate(360deg);}}
</style>



