<template>
  <div class="VipOrder">
      <h3>VIP订购</h3>
      <div class="VipExplain">
          1、兑换完成后,资源下载权益、VIP优惠券、VIP免费体验权益将直接充入您的帐号。所有权益自兑换之日起,有效期2个月,过期后失效,且不退积分。…
      </div>
      <div class="VipButton">
          <ul>
              <li>支付宝订购</li>
              <li>微信订购</li>
              <li>积分换购</li>
          </ul>
      </div>
      <div class="vipBanner">
          <div class="vipbanner_1">查看个人账单详情信息</div>
          <div class="vipbanner_2"><div class="userCenter">进入个人中心</div></div>
      </div>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.VipOrder h3 {
  position: relative;
  padding: 25px 0px 0px 71px;
  width: 300px;
  height: 62px;
  font-family: Arial, Helvetica, sans-serif;
  color: #6687a7;
  font-size: 44px;
  font-weight: 400;
}
.VipExplain {
  position: absolute;
  top: 207px;
  left: 728px;
  width: 480px;
  height: 180px;
  line-height: 40px;
  text-align: left;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  color: #ffffff;
}
.VipButton {
  position: absolute;
  top: 375px;
  left: 728px;
}
.VipButton li {
  width: 142px;
  height: 54px;
  line-height: 54px;
  text-align: center;
  border-radius: 12px;
  color: #ffffff;
  background-color: #58687a;
  float: left;
  margin-right: 18px;
  font-size: 20px;
}
.vipBanner {
  position: absolute;
  display: flex;
  justify-content: space-between;
  top: 554px;
  left: 82.5px;
  width: 1112px;
  height: 91px;
  border-radius: 19px;
  background-color: #c19b54;
}
.vipbanner_1 {
  width: 300px;
  height: 33px;
  color: #ffffff;
  font-size: 25px;
  font-family: Arial, Helvetica, sans-serif;
  padding: 28px 52px;
}
.vipbanner_2 {
  width: 194px;
  height: 56px;
  padding: 17px 21px;
}
.userCenter {
  width: 192px;
  height: 57px;
  font-size: 22px;
  text-align: center;
  line-height: 57px;
  border-radius: 12px;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.2);
}
</style>
<script>
export default {
  name: "VipOrder",
  props: [],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      round: "round"
    };
  },
  created() {},
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
      }
    },
    left() {},
    right() {},
    up() {},
    down() {},
    addCss() {},
    removeCss() {},
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 310;
      return realHeight;
    }
  },
  watch: {}
};
</script>



