<template>
    <div class="userInfoAndDeviceInfo">
        <div class="infoName" v-if="type==0"><span>{{title}}</span></div>
        <div class="infoName" v-if="type==1"><span>系统信息</span></div>
        
        <div class="infoShowBox">
            <ul>
                <template v-if="type==0">
                    <li><div>账号</div><div>{{deviceInfo.user_id}}</div></li>
                    <li><div>Mac地址</div><div>{{deviceInfo.mac}}</div></li>
                    <li><div>开发版本</div><div>{{deviceInfo.apk_version_name}}</div></li>
                </template>
                 <template v-else>
                    <li><div>型号</div><div>{{deviceInfo.brand}}</div></li>
                    <li><div>安卓版本</div><div>{{deviceInfo.sys_v}}</div></li>
                    <li><div>固件版本</div><div>{{deviceInfo.soft_v}}</div></li>
                    <li><div>开发版本</div><div>{{deviceInfo.apk_version_name}}</div></li>
                    <li><div>IP地址</div><div>{{deviceInfo.ip}}</div></li>
                    <li><div>MAC地址</div><div>{{deviceInfo.mac}}</div></li>
                </template>
            </ul>
        </div>
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.userInfoAndDeviceInfo {
  position: relative;
  width: 1280px;
  height: 720px;
}
.userInfoAndDeviceInfo .infoName {
  position: relative;
  width: 1280px;
  height: 136px;
  padding: 84px 60px 0px 96px;
}
.infoName span {
  font-size: 35px;
}
.infoShowBox {
  position: relative;
  display: flex;
  align-items: center;
  width: 630px;
  height: 320px;
  background-color: #4682b4;
  left: 160px;
  padding: 30px 120px;
}
.infoShowBox li {
  font-size: 25px;
  margin: 0px 0px;
  height: 55px;
  line-height: 55px;
}
.infoShowBox li div {
  display: inline-block;
  width: 300px;
}
</style>
<script>
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import Toast from "../../tool/toast/toast.js";
export default {
  props: ["type"],
  name: "UserInfoAndDeviceInfo",
  data() {
    return {
      title: "账号信息",
      isShow: true,
      deviceInfo: {
        reserve_group: "yanhuauer",
        model: "",
        content_id: "",
        terrace_id: "",
        operator_id: "",
        apk_version: "7",
        brand: "",
        site_id: "19",
        user_id: "yanhua123",
        device_id: "2018042746ac4afeb380cc",
        soft_v: "3.0.1",
        sys_v: "4.4.2",
        mode: "",
        clickTime: 0
      }
    };
  },
  created() {
    this.getInfo();
  },
  activated() {
    this.clickTime = 0;
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        if(window.localStorage.getItem('roter') == 'user'){
          window.localStorage.removeItem('roter')
          this.$router.push({name:'user'});
        }else{
          if(!this.isSouth){
            this.$router.push({name:'index'});
          }else{
            this.$router.push({name:'south'});
          }
        }
      } else if (kc == "KeyEnter") {
        this.clickTime++;
        if (this.clickTime == 5) {
          localStorage.removeItem("Henan_Category");
          localStorage.removeItem("AuthResult");
          localStorage.removeItem("AAA");
          Toast("终端缓存清除成功，请重启客户端!");
        }
      }
    },
    getInfo() {
      this.deviceInfo = JSON.parse(yh.deviceInfo());
      console.log("终端的信息:" + this.deviceInfo);
      console.log(this.deviceInfo);
    }
  },
  computed: {
    ...mapGetters(["category", "navpos","isSouth"])
  },
  watch: {}
};
</script>



